.Container {
    background: #ECEFF1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.Header {
    background: rgb(51, 51, 51);
    width: 100%;
    font-family: Helvetica, Arial, sans-serif;
    color: white;
    padding: 20px;
  }
  
  .title {
    float: left;
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  .scores {
    float: right;
    font-size: 1.2rem;
  }
  
.Instructions {
    background: rgb(51, 51, 51);
    width: 100%;
    font-family: Helvetica, Arial, sans-serif;
    color: white;
    margin-bottom: 10px;
    padding: 20px;
    text-align: center;
  }
.Card {
    position: relative;
    text-align: center;
    padding: 10px;
  }
  

  
  
